import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_EMPTY_SELECTION = Symbol('TABLE_EMPTY_SELECTION');

export function emptyTableSelection(table: Table) {
  return {
    type: TABLE_EMPTY_SELECTION,
    payload: {
      node: table,
    },
  };
}

interface TableEmptySelectionPayload {
  node: Table;
}

interface TableEmptySelectionAction {
  type: typeof TABLE_EMPTY_SELECTION;
  payload: TableEmptySelectionPayload;
}

export function isTableEmptySelectionAction(
  action: Action,
): action is TableEmptySelectionAction {
  return action.type === TABLE_EMPTY_SELECTION;
}
